import { ContextApi } from 'contexts/Localization/types'
import { PageMeta } from './types'

export const DEFAULT_META: PageMeta = {
  title: 'EVERTELL FLIX',
  description:
    'The most popular AMM on BSC by user count! Earn EFLX through yield farming or win it in the Lottery, then stake it in Syrup Pools to earn more tokens! Initial Farm Offerings (new token launch model pioneered by EVERTELL FLIX), NFTs, and more, on a platform you can trust.',
  image: 'https://app.evertell.app/logo.png',
}

export const getCustomMeta = (path: string, t: ContextApi['t']): PageMeta => {
  switch (path) {
    case '/':
      return {
        title: `${t('Home')} | ${t('EVERTELL FLIX')}`,
      }
    case '/competition':
      return {
        title: `${t('Trading Battle')} | ${t('EVERTELL FLIX')}`,
      }
    case '/prediction':
      return {
        title: `${t('Prediction')} | ${t('EVERTELL FLIX')}`,
      }
    case '/farms':
      return {
        title: `${t('Farms')} | ${t('EVERTELL FLIX')}`,
      }
    case '/pools':
      return {
        title: `${t('Pools')} | ${t('EVERTELL FLIX')}`,
      }
    case '/lottery':
      return {
        title: `${t('Lottery')} | ${t('EVERTELL FLIX')}`,
      }
    case '/collectibles':
      return {
        title: `${t('Collectibles')} | ${t('EVERTELL FLIX')}`,
      }
    case '/ifo':
      return {
        title: `${t('Initial Farm Offering')} | ${t('EVERTELL FLIX')}`,
      }
    case '/teams':
      return {
        title: `${t('Leaderboard')} | ${t('EVERTELL FLIX')}`,
      }
    case '/profile/tasks':
      return {
        title: `${t('Task Center')} | ${t('EVERTELL FLIX')}`,
      }
    case '/profile':
      return {
        title: `${t('Your Profile')} | ${t('EVERTELL FLIX')}`,
      }
    default:
      return null
  }
}
